/* Body */
body {
    overflow: auto;
    position: relative;
    background-color: black;
    margin: 0;
    height: 100vh;
    overflow: hidden;
}

body::after {
    content: "";
    background-image: url(/static/media/marble_bg.f9170535.svg);
    opacity: 0.5;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    position: absolute;
    z-index: -1;
}

.overlay {
    opacity: 0.5;
}

/* Header */
header {
    background-color: black;
    opacity: 1;
    justify-content: space-between;
    align-items: center;
    display: flex;
}

header a img {
    margin: 0em 3em;
}

nav {
    margin: 4em;
}

nav ul {
    list-style-type: none;
    display: flex;
    grid-gap: 3rem;
    gap: 3rem;
    margin: 0;
}

nav ul li a {
    text-decoration: none;
    color: white;
    text-transform: uppercase;
    font-family: mainFont;
    font-size: 1.6rem;
    position: relative;
}

nav ul li a.active:before {
    position: absolute;
    content: "";
    width: 35%;
    height: 1px;
    border-bottom: 2px solid white;
    bottom: -6px;
}


nav ul li a:before {
    position: absolute;
    content: "";
    width: 0%;
    height: 1px;
    border-bottom: 2px solid rgb(122, 122, 122);
    bottom: -6px;
    transition: width .3s;
}

/* Main */

main {
    grid-gap: 8em;
    gap: 8em;
    margin-top: 1em;
}


/* Slides */
.slide-container {
    max-width: 1200px;
    position: relative;
    margin: auto;
}

.slide {
    display: none;
    cursor: pointer;
}

/* Brand Texts*/
.tesla {
    color: #f2f2f2;
    font-size: 200px;
    font-family: TeslaFont;
    opacity: 0.5;
    text-align: center;
}

.porsche {
    color: #f2f2f2;
    font-size: 150px;
    font-family: PorscheFont;
    opacity: 0.5;
    text-align: center;
    margin-bottom: 50px;
}

.maserati {
    color: #f2f2f2;
    font-size: 150px;
