    opacity: 0.5;
    text-align: center;
}

.jaguar {
    color: #f2f2f2;
    font-size: 200px;
    font-family: JaguarFont;
    opacity: 0.5;
    text-align: center;
}

.car-container {
    position: relative;
    width: 200px;
    height: 100px;
}

.car {
    position: absolute;
    top: 0;
    transition: opacity 0.5s, -webkit-transform 0.5s;
    transition: transform 0.5s, opacity 0.5s;
    transition: transform 0.5s, opacity 0.5s, -webkit-transform 0.5s;
}

.car1 {
    left: 50px;
}

.car2 {
    left: 250px;
}

.car3 {
    left: 450px;
}

.car4 {
    left: 650px;
}

.car:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    z-index: 10;
    opacity: 1;
}


/* Slide Dots*/

.slideNav {
    position: absolute;
    bottom: 10px;
    right: 10px;
}

.dot {
    cursor: pointer;
    height: 15px;
    width: 15px;
    margin: 2px;
    background-color: #bbb;
    border-radius: 50%;
    display: inline-block;
    transition: background-color 0.6s ease;

}

span.active,
.dot:hover {
    background-color: #717171;
}

/* Microinteractions */

nav ul li a:hover:before {
    width: 35%;
}

