/* Style the flip card container */
.flip-card {
    position: relative;
    background-color: transparent;
    width: 300px;
    height: 400px;
    -webkit-perspective: 1000px;
            perspective: 1000px;
    border-radius: 15px;
}

/* Style the flip card */
.flip-card-inner {
    position: relative;
    width: 100%;
    height: 100%;
    text-align: center;
    transition: -webkit-transform 0.6s;
    transition: transform 0.6s;
    transition: transform 0.6s, -webkit-transform 0.6s;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
}

/* Style the front and back of the flip card */
.flip-card-front,
.flip-card-back {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    border-radius: 15px;
    padding: 20px;
    border: 2px solid #AF9E64;
}

/* Style the front of the flip card */
.flip-card-front {
    background-color: rgba(26, 26, 26, 0.8);

    justify-content: space-between;
}

/* Style the back of the flip card */
.flip-card-back {
    background-color: rgba(76, 0, 0, 0.8);
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
}

